export function SpotInvestments(quality, channels) {
    const quality_arr = [1500, 1900, 2700, 3700, 5500];
    const base = quality_arr[quality - 1];
    return base + (channels.length - 1) * base * 0.5;
}

export function SpotOperationsCosts(channels, segments, marketSize) {
    let res = 0;
    channels.forEach(channel => {
        switch(channel) {
            case 'TV': res+= 170000;
            break;
            case 'visual outdoor ads': res+= 90000;
            break;
            case 'autoradio podcasts': res+= 40000;
            break;
            case 'postal mailings':  res+= marketSize * 0.4; 
            break;
            case 'SMM': 
                res+=  segments.reduce((sum, segment) => {
                    return sum+= segment.segment.peak_size * 0.003
                }, 0);
                
            break;
            case 'content marketing': 
                res+= segments.reduce((sum, segment) => {
                    return sum+= segment.segment.peak_size * 0.002
                }, 0);
            break;
            case 'influencers':
                res+= segments.reduce((sum, segment) => {
                    return sum+= segment.segment.peak_size * 0.05
                }, 0);
        }   
    })
    return res;
}