<template>
    <v-dialog v-model="dialog" scrollable max-width="600px" hide-overlay>
        <template v-slot:activator="{ on }">
            <v-btn color="secondary" class="text-lowercase mr-4" dark small v-on="on">add resources</v-btn>
        </template>
        <v-card v-if="!loading">
            <v-card-title class="subtitle-2 pt-6 text-pre-wrap">
                New resources by <span class="secondary--text">{{ set.item }}</span> set
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text class="black--text">
                <v-container grid-list-md>
                    <v-form v-model="valid" ref="form" validation>
                        <v-row>
                            <v-col cols="12" md="4" sm="6">
                                <v-select dense return-object v-model="team"
                                    :items="teams" item-text="name" item-value="id"
                                    item-color="secondary" label="Team" 
                                    @change="onceMore()" 
                                    :rules="[rules.required]">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="Capacities current round"
                                    :value="formatNumber(capacities)" 
                                    name="capacities" type="text"
                                    readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="Capacities increase for next round"
                                    v-model.number="capacities_increase" 
                                    name="capacities_increase" type="text"
                                    readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="Production assets"
                                    :value="formatNumber(production_assets)" 
                                    name="production_assets" type="text"
                                    readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="Long-term debt"
                                    :value="formatNumber(long_term_debt)" 
                                    name="debt" type="text"
                                    readonly>
                                </v-text-field>
                            </v-col> 
                            <v-col cols="12" sm="6" md="3">
                                <v-text-field dense color="secondary" 
                                    label="Borrowins this round"
                                    :hint="borrowHint"
                                    v-model.number="borrowings" 
                                    name="borrow" type="text"
                                    readonly>
                                </v-text-field>
                            </v-col>   
                        </v-row>
                    </v-form>
                </v-container>
                <v-row justify="center" class="error--text">{{ error }}</v-row>
                <v-row justify="center" class="text-pre-wrap subtitle-2">
                    {{ message }}
                </v-row>    
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="secondary" text class="text-capitalize" :disabled="Boolean(error)"
                    @click="addResource">Save
                </v-btn>

                <v-btn color="secondary" text class="text-capitalize" @click="closeDialog">
                    Close
                </v-btn>

            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import { localeNumber } from '../../../functions/common.js';

export default {
    name: 'AddResource',
    props: ['set'],
    data() {
        return {
            dialog: false,
            valid: false,
            error: '',
            message: '',
            rules: {
                required: v => !!v || 'this field is required',
            },
            //props
            team: null,
            capacities_increase: 0,
            borrowings: 0,
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        locale() {
            return this.$store.getters.locale;
        },
        teams() {
            return this.$store.getters.teams;
        },
        market_segments() {
            return this.$store.getters.market_segments;
        },
        capacities: {
            get() {
                let res = 0;
                if (this.market_segments?.length > 0) {
                    res = this.market_segments.reduce((sum, seg) => {
                        return sum += seg.segment.peak_size;
                    }, 0)
                }
                return res * 0.1;
            },
            set(val) {
                return val;
            } 
        },
        production_assets() {
            if (this.capacities) {
                return this.capacities * 10;
            }
            return 0;
        },
        long_term_debt() {
            if (this.capacities) {
                return this.capacities * 5;
            }
            return 0;
        },
        contributed_capital() {
            let res = 0;
            if (this.production_assets) {
                res = this.production_assets * 2;
            }
            return res;
        },
        borrowHint() {
           return `not more than $${localeNumber(this.fix_assets * 0.7 - this.debt, this.locale)}`;
        }  
    },
    watch: {
        async set() {
            const obj = {
                gameId: this.set.value,
                round: 0
            }
            try {
                await this.$store.dispatch('fetchTeams', this.set.value);
            }
            catch (error) {
                console.log(error)
                await this.$store.dispatch('setError', error.message)
            }
            try {
                await this.$store.dispatch('fetchMarketSegments', obj)
            }
            catch (error) {
                console.log(error)
                await this.$store.dispatch('setError', error.message)
            }
        },
    },
    methods: {
        formatNumber(num) {
            return localeNumber(num, this.locale);
        },
        onceMore() {
            this.error = ''
            this.calculate = false;
            this.message = '';
            this.capacities = () => {
                let res = 0;
                if (this.market_segments?.length > 0) {
                    res = this.market_segments.reduce((sum, seg) => {
                        return sum += seg.segment.peak_size;
                    }, 0)
                }
                return res * 0.1;
            }
        },
        async addResource() {

            if (this.$refs.form.validate()) {
                /*const segments = this.pick_segments.map(seg => seg.segment.name);*/

                const resource = {
                    gameId: this.set.value,
                    round: 0,
                    team: this.team,
                    capacities: this.capacities,
                    capacities_increase: this.capacities_increase,
                    long_term_debt: this.long_term_debt,
                    borrowings: this.borrowings,
                    research_segments: [],
                    research_costs: 0,
                }

                const intermediary = {
                    gameId: this.set.value,
                    round: 0,
                    teamId: this.team.id,
                    production_assets: this.production_assets,
                    retail_assets: 0,
                    production_depreciation: 0,
                    retail_depreciation: 0,
                    franchise_cost: 0,
                    franchise_fee: 0,
                    retail_entry: 0,
                    retail_annual_fee: 0,
                    invest_assets: 0,
                    market_research: 0,
                } 
                
                const cash = (this.contributed_capital + this.long_term_debt) - this.production_assets;
                const finances = {
                    gameId: this.set.value,
                    round: 0,
                    team: this.team,
                    fixed_assets: 0,
                    cash: cash,
                    contributed_capital: this.contributed_capital,
                    long_term_debt: this.long_term_debt,
                }
                /*console.log(resource);*/
                //console.log(finances);
                try {
                    //add resource, intermediary, finances
                    await this.$store.dispatch('addResource', resource);
                    await this.$store.dispatch('addIntermediary', intermediary);
                    await this.$store.dispatch('addFinances', finances);
                    
                    this.closeDialog()
                }
                catch (error) {
                    this.error = `${error.message}`;
                    this.message = '';
                    this.$store.dispatch('setError', error.message)//throw error
                }  
            } 
        },
        closeDialog() {
            //reset
            this.dialog = false;
            this.error = '';
            this.message = '';

           /*  this.$refs.form.reset();
            this.team = null; */
        }
    },
    async mounted() {
        const obj= {
            gameId: this.set.value,
            round: 0
        }
        if (this.teams?.length < 1) {
            try {
                await this.$store.dispatch('fetchTeams', this.set.value);
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
        if (this.market_segments?.length < 1) {
            try {
                await this.$store.dispatch('fetchMarketSegments', obj)
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
    }
}
</script>