<template>
    <v-dialog v-model="dialog" scrollable max-width="600px" hide-overlay>
        <template v-slot:activator="{ on }">
            <v-btn color="secondary" class="text-lowercase mr-4" dark small v-on="on">add spot</v-btn>
        </template>
        <v-card v-if="!loading">
            <v-card-title class="subtitle-2 pt-6 text-pre-wrap">
                New spot parameters by <span class="secondary--text">{{ set.item }}</span> set
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text class="black--text">
                <v-container grid-list-md>
                    <v-form v-model="valid" ref="form" validation>
                        <v-row>
                            <v-col cols="12" md="4" sm="6">
                                <v-select dense return-object
                                    v-model="team"
                                    :items="teams" 
                                    item-text="name"
                                    item-value="id"
                                    item-color="secondary" 
                                    label="team" 
                                    @change="onceMore()"
                                    :rules="[rules.required]">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field dense color="primary" 
                                    label="spot's quality*"
                                    v-model.number="quality"
                                    hint="it's expected number between 1 and 5"
                                    persistent-hint
                                    name="quality" 
                                    type="text"
                                    :rules="[rules.required, rules.number, rules.max]"
                                    @change="onceMore()">
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-select dense 
                                    color="secondary" 
                                    label="core message*"
                                    v-model="core_message" 
                                    :items="core_messages"
                                    item-color="secondary" 
                                    :rules="[rules.required]" @change="onceMore()">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="12" md="12">
                                <v-select dense multiple
                                    color="secondary" 
                                    label="channels*" 
                                    v-model="channels" 
                                    :items="all_channels"
                                    item-color="secondary" 
                                    :rules="[rules.required]"
                                    @change="onceMore()">
                                </v-select>
                            </v-col> 
                            <v-col cols="12" md="6" sm="6">
                                <v-select dense return-object
                                    v-model="trademark" 
                                    :items="filteredTrademarks" 
                                    item-text="name"
                                    item-value="id"
                                    color="secondary" 
                                    item-color="secondary" 
                                    label="trademark*" 
                                    @change="onceMore()"
                                    :rules="[rules.required]">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="6">
                                <v-select dense color="secondary"
                                    return-object
                                    label="advanced feature"
                                    v-model="af" 
                                    :items="features"
                                    item-text="text"
                                    item-value="value"
                                    item-color="secondary" 
                                    @change="onceMore()">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-select dense clearable
                                    color="secondary" 
                                    label="price" 
                                    v-model="price" 
                                    :items="prices"
                                    item-color="secondary" 
                                    @change="onceMore()">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-select dense
                                    color="secondary" 
                                    label="objective*" 
                                    v-model="objective" 
                                    :items="objectives"
                                    item-color="secondary" 
                                    :rules="[rules.required]"
                                    @change="onceMore()">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="4">
                                <v-text-field dense color="secondary" 
                                    label="spot name" 
                                    v-model="name" 
                                    type="text"
                                    readonly>
                                </v-text-field>
                            </v-col>
                            <v-col cols="12" sm="6" md="6">
                                <v-select dense 
                                    color="secondary" 
                                    label="value*" 
                                    v-model="value" 
                                    :items="values"
                                    item-text="text"
                                    item-value="value"
                                    item-color="secondary" 
                                    :rules="[rules.required]"
                                    @change="onceMore()">
                                </v-select>
                            </v-col>
                            <v-col cols="12" sm="6" md="6">
                                <v-select dense multiple
                                    return-object
                                    v-model="pick_segments" 
                                    :items="segments" 
                                    item-text="segment.name"
                                    item-value="segment.pick_size"
                                    color="secondary" 
                                    item-color="secondary" 
                                    label="segments (multiple)*" 
                                    @change="onceMore()"
                                    :rules="[rules.required]">
                                </v-select>
                            </v-col>
                        </v-row>
                    </v-form>
                </v-container>
                <v-row justify="center" class="error--text">{{ error }}</v-row>
                <v-row justify="center" class="subtitle-2">{{ message }}</v-row>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text class="text-capitalize" 
                    :disabled="!valid || Boolean(error)"
                    @click="calculating">
                    Calculate
                </v-btn>
                
                <v-btn color="secondary" text class="text-capitalize" 
                    :disabled="!calculate || Boolean(error)"
                    @click="addSpot">Save
                </v-btn>

                <v-btn color="secondary" text class="text-capitalize" 
                    @click="closeDialog">
                    Close
                </v-btn>

            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
import { SpotInvestments, SpotOperationsCosts } from '../../../functions/promotion.js'
export default {
    name: 'AddSpot',
    props: ['set'],
    data() {
        return {
            dialog: false,
            valid: false,
            error: '',
            message: '',
            calculate: false,
            rules: {
                required: v => !!v || 'this field is required',
                number: v => /^[+]?[0-9]/.test(v) || 'it\'s expected number',
                max: v => v <= 5 || 'max 5 expected',
            },
            
            //props
            trademark: null,
            core_message: '',
            af: '',
            value: '',
            pick_segments: [],
            channels: [],
            team: null,
            price: '',
            objective: '',
            quality: 1,
            investments: 0,
            operations: 0,
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        locale() {
            return this.$store.getters.locale;
        },
        teams() {
            return this.$store.getters.teams;
        },
        segments() {
            return this.$store.getters.market_segments;
        },
        trademarks() {
            return this.$store.getters.trademarks;
        },
        all_channels() {
            return this.$store.getters.CHANNELS;
        },
        core_messages() {
            return this.$store.getters.CORE_MESSAGES;
        },
        features() {
            return this.$store.getters.FEATURES;
        },
        prices() {
            return this.$store.getters.PRICES;
        },
        objectives() {
            return this.$store.getters.OBJECTIVES;
        },
        values () {
            return this.$store.getters.VALUES;
        },
        filteredTrademarks() {
            if (this.team) {
                const arr = [];
                this.team?.products.map(prod => {
                    const inArr = arr.find(it => it === prod.trademarkId)
                    if (!inArr) {
                        arr.push(prod.trademarkId)
                    }
                })
                const res = this.trademarks.filter(it => arr.indexOf(it.id) !== -1)
                return res
            }
            return this.trademarks
        },
        marketSize() {
            let res = 0;
            if (this.segments.length > 0) {
                res = this.segments.reduce((sum, seg) => {
                    return sum+= seg.segment.peak_size;
                }, 0)
            }
            console.log(res)
            return res;
        },
        name:{ 
            get() {
                if (this.trademark) {
                    return `${this.trademark.name} Ad`
                }
                return ''
            },
            set(val) {
                return val;
            }
        }
    },
    watch: {
        async set() {
            try {
                await this.$store.dispatch('fetchTeams', this.set.value);
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
    },
    methods: {
        localeNumber(num) {
            if (this.locale == 'ro-RO') {
                return num.toLocaleString('en-US')
            }
            return num.toLocaleString(this.locale)
        },
        onceMore() {
            this.error = ''
            this.calculate = false;
            this.message = '';
        },
        calculating() {
            if (this.$refs.form.validate()) {
                
                this.investments = SpotInvestments(this.quality, this.channels);
                this.operations = SpotOperationsCosts(this.channels, this.pick_segments, this.marketSize)

                this.message = `spot investments = ${this.localeNumber(this.investments)} & 
                    operational costs = ${this.localeNumber(this.operations)}`;
                this.calculate = true;
            }
        },
        async addSpot() {

            if (this.$refs.form.validate()) {

                const segments = this.pick_segments.map(seg => seg.segment.name);
                const advanced_feature = this.af.text ? this.af.text : '';
                
                const spot = {
                    gameId: this.set.value,
                    round: 0,
                    register_round: 0,
                    team: this.team,
                    name: this.name,
                    advanced_feature: advanced_feature,
                    price: this.price,
                    value: this.value,
                    objective: this.objective,
                    quality: this.quality,
                    trademark: this.trademark,
                    core_message: this.core_message,
                    segments: segments,
                    channels: this.channels,
                    investments: this.investments,
                    operations: this.operations,
                }
                 console.log(spot);
                try {
                    //add spot 
                    await this.$store.dispatch('addSpot', spot);
                    this.closeDialog()
                }
                catch (error) {
                    this.error = `${error.message}`;
                    this.message = '';
                    this.$store.dispatch('setError', error.message)//throw error
                }
            }
        },
        closeDialog() {
            //reset
            this.dialog = false;
            this.error = '';
            this.message = '';
            this.calculate = false;

            this.name = '';
            this.$refs.form.reset();
            this.trademark = [];
            this.design_type = ''
            this.af = ''
            this.value = ''
            this.pick_segments = []
            this.channels = []
            this.team = null
            this.price = ''
            this.objective = ''
            this.quality = 1
        }
    },
    async mounted() {
        
        if (this.trademarks.length < 1) {
            try {
                await this.$store.dispatch('fetchTrademarks');
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
        if (this.teams.length < 1) {
            try {
                await this.$store.dispatch('fetchTeams', this.set.value);
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
        if (this.segments.length < 1) {
            const params = {
                gameId: this.set.value,
                round: 0,
            }
            try {
                await this.$store.dispatch('fetchMarketSegments', params)
            }
            catch (error) {
                await this.$store.dispatch('setError', error.message)
            }
        }
    }
}

</script>