<template>
   <v-container fluid>
        <div v-if="!loading">
            <v-row 
                class="my-2 subtitle-2 secondary--text pl-12">
                Teams
            </v-row>
            <!--table head-->
            <v-row class="subtitle-2" no-gutters>
                <v-col v-for="(item, i) in headers" :key="i" 
                    class="tabCell"
                    :cols="item.cols"
                    :class="item.style">
                    {{ item.title }}
                </v-col>
            </v-row>
    
            <div v-if="teams.length">
                <!--teams tab-->
                <v-row v-for="team in teams" :key="team.id" no-gutters>
                    <v-col cols="1" class="tabCell text-left">{{ team.id }}</v-col>
                    <v-col cols="2" class="tabCell text-center">{{ team.name }}</v-col>
                    <v-col cols="1" class="tabCell text-center">{{ team.gameId }}</v-col>
                </v-row>
            </div>
        </div>
    </v-container>
</template>
<script>
export default {
    data() {
        return {
            headers: [
                { title: 'Team id', style: 'text-left', cols: 1 },
                { title: 'Team', style: 'text-center', cols: 2 },
                { title: 'Game id', style: 'text-center', cols: 1 },
            ],
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        locale() {
            return this.$store.getters.locale;
        },
        teams() {
            return this.$store.getters.teams
        }
    },
    methods: {
        localeNumber(num) {
            if (this.locale == 'ro-RO') {
                return num.toLocaleString('en-US')
            }
            return num.toLocaleString(this.locale)
        }
    },
    components: {}
}
</script>