<template>
    <v-container fluid>
        <div v-if="!loading">
            <v-row class="my-2 subtitle-2 secondary--text pl-12">
                Promotion
            </v-row>
            <div v-if="spots?.length > 0">
                <!--spots table head-->
                <v-row class="subtitle-2" no-gutters>
                    <v-col v-for="(item, i) in headers" :key="i" 
                        class="tabCell"
                        :class="item.style">
                        {{ item.title }}
                    </v-col>
                </v-row>
                <!-- spots tab -->
                <v-row v-for="spot in spots" :key="spot.id" no-gutters>
                    <v-col class="tabCell text-left">{{ spot.name }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.team?.name }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.trademark?.name }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.core_message }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.advanced_feature }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.price }}</v-col>
                    <v-col class="tabCell text-center">{{ getValue(spot.value) }}</v-col>
                    <v-col class="tabCell text-center">{{ selializing(spot.channels) }}</v-col>
                    <v-col class="tabCell text-center">{{ selializing(spot.segments) }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.objective }}</v-col>
                    <v-col class="tabCell text-center">{{ spot.quality }}</v-col>
                    <v-col class="tabCell text-center">{{ localeNumber(spot.investments) }}</v-col> 
                    <v-col class="tabCell text-center">{{ localeNumber(spot.operations) }}</v-col>   
                    <v-col class="tabCell text-center">
                        <delete-spot :spot="spot" />
                    </v-col>
                </v-row>
                </div>
                <v-row v-else class="mt-8 px-4" justify="center">
                    You haven't spots yet!
                </v-row>
        </div>
        <!--!loading-->
        <div v-else class="text-center">
            <loader></loader>
        </div>
    </v-container>
</template>
<script>
import deleteSpot from '../../../components/admin/spots/deleteSpot.vue'
import Loader from '../../../layouts/Loader.vue'
export default {
    data() {
        return {
            headers: [
                { title: 'spot\'s name', style: 'text-left' },
                { title: 'team', style: 'text-center' },
                { title: 'trademark', style: 'text-center' },               
                { title: 'core message', style: 'text-center' },
                { title: 'advanced feature', style: 'text-center' },
                { title: 'price', style: 'text-center' },
                { title: 'value', style: 'text-center' },
                { title: 'ad channels', style: 'text-center' },
                { title: 'segments', style: 'text-center' },
                { title: 'objective', style: 'text-center' },
                { title: 'level quality', style: 'text-center' },
                { title: 'investments in creation', style: 'text-center' },
                { title: 'operations costs', style: 'text-center' },
                { title: 'action', style: 'text-center' },
            ],
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading;
        },
        locale() {
            return this.$store.getters.locale;
        },
        spots() {
            return this.$store.getters.spots_all;
        },
        values () {
            return this.$store.getters.VALUES;
        }
    },
    methods: {
        localeNumber(num) {
            if (this.locale == 'ro-RO') {
                return num.toLocaleString('en-US')
            }
            return num.toLocaleString(this.locale)
        },
        getValue(val) {
            return this.values?.find(v => v.value == val).text;
        },
        selializing(arr) {
            return arr.join(', ');
        }
    },
    components: {deleteSpot, Loader}
}
</script>