const ONLINE_INVESTMENTS = 28000;
const OFFLINE_INVESTMENTS = 15000;
const ONLINE_OPERATIONAL_COSTS = 40000;
const OFFLINE_OPERATIONAL_COSTS = 33000;

export function retailInvestments(type, stores = 1) {
    if(type == 1) {
        return ONLINE_INVESTMENTS;
    }
    else if (type == 2 || type == 3) {
        return OFFLINE_INVESTMENTS * stores;
    }
    else {
        throw new Error(`undefined channel type: ${type}`);
    }
}

export function operationalCosts(type, stores = 1) {
    if (type == 1) {
        return ONLINE_OPERATIONAL_COSTS;
    }
    else if (type == 2 || type == 3) {
        return OFFLINE_OPERATIONAL_COSTS * stores;
    }
    else {
        throw new Error(`undefined channel type: ${type}`);
    }
    
}