<template>
    <v-container fluid>
        <div v-if="!loading">
            <v-row class="my-2 subtitle-2 secondary--text pl-12">
                Games
            </v-row>
            <div v-if="games.length > 0">
                <!--games table head-->
                <v-row class="subtitle-2" no-gutters>
                    <v-col v-for="(item, i) in headers" :key="i" 
                        class="tabCell"
                        :class="item.style">
                        {{ item.title }}
                    </v-col>
                </v-row>
                <!-- games tab -->
                <v-row v-for="game in games" :key="game.id" no-gutters>
                    <v-col class="tabCell text-left">{{ game.id }}</v-col>
                    <v-col class="tabCell text-center">{{ game.category }}</v-col>
                    <v-col class="tabCell text-center">{{ game.description }}</v-col>
                    <v-col class="tabCell text-center">{{ game.userId }}</v-col>
                        
                    <v-col class="tabCell text-center">
                        <!-- <delete-game :game="game" /> -->
                    </v-col>
                </v-row>
            </div>
            <v-row v-else class="mt-8 px-4">
                You haven't games yet!
            </v-row>
        </div>
    </v-container>
</template>
<script>
export default {
    name: 'Games',
    data() {
        return {
            headers: [
                { title: 'game id', style: 'text-left' },
                { title: 'category', style: 'text-center' },
                { title: 'description', style: 'text-center' },
                { title: 'user id', style: 'text-center' },
                { title: 'action', style: 'text-center' },
            ]
        }
    },
    computed: {
        loading() {
            return this.$store.getters.loading
        },
        locale() {
            return this.$store.getters.locale;
        },
        games() {
            return this.$store.getters.games;
        }
    },
    methods: {
        dateToString(date) {
            return date.toLocaleString('en-GB', { timeZone: 'UTC' }).substr(0, 10);
        },
    },
    components: {  },
    async mounted() {
    
        if (this.games.length < 1) {
            try {
                await this.$store.dispatch('fetchUserGames')
            }
            catch (error) {
                console.log(error.message);
            }
        }
    }
}
</script>